body {
    background-color: #555;
    margin: 0;
    padding: 15px;
    font-family: sans-serif;
    --mainText-color: #99f1ff;
    --subText-Color: #D0D0D0;
    --shadows: 1px 4px 4px 0px #00FFFF;
    --border: 1px solid #333;
}
.profile{
    border:var(--border);
    /* height: 20%; */
    -webkit-box-shadow: var(--shadows);
            box-shadow: var(--shadows);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    background-color: #888;
}
    .profile img {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
        width: 20%;
        height: auto;
        display:inline-block;
    }
    @-webkit-keyframes App-logo-spin {
        from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
        to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    }
    @keyframes App-logo-spin {
        from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
        to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    }
    .profile div{
        text-align: left;
    }
        .profile h1{
            color:var(--mainText-color);
        }
        .profile h4{
            color: var(--subText-Color);
        }

.clock{
    text-align: center;
    color:#eee;
}

/*parent of project*/
.projects{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
/*Parent / project*/
.project{
    width:100%;
    -webkit-box-shadow: var(--shadows);
            box-shadow: var(--shadows);
    border:var(--border);
    margin: 8px 8px;
    background-color: #888;
}
    /*child of project*/
    .project .projectTitle{
        /* height: 10vh; */
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: start;
            justify-content: flex-start;
    }
        /*child of projectTitle*/
        .project .displayImg {
            margin: 5px;
            width:10%;
            height: auto;
            border:1px solid #333;
            padding: 10px;
            /* display: flex; */
        }
        .project .displayImg:hover{
            -webkit-box-shadow: var(--shadows);
                    box-shadow: var(--shadows);
        }
        .project div{
            /* text-align: left; */
            margin: 5px;
        }
            /*child of projectTitle/div*/
            .project a {
                color:var(--mainText-color);
            }
            .project a:visited {
                color:#33e4ff;
            }
            .project h2{
                color:var(--mainText-color);
            }
            .project h5{
                color: var(--subText-Color);
            }
    /*child of project*/
    .projectImgs{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction:column;
            flex-direction:column;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        --main-width: 50%;

    }
    @media (min-width: 375px){
        .projectImgs{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction:row;
                flex-direction:row;
            -ms-flex-pack: distribute;
                justify-content: space-around;
            --main-width:25%;
        }
    }

        /*child of projectImgs*/
        .projectImgs img{
            margin: 3px 0px;
            width:var(--main-width); /* css variable */
            border:1px solid #333;
        }
        .projectImgs img:hover{
            -webkit-box-shadow: var(--shadows);
                    box-shadow: var(--shadows);
        }
    /*child of project*/
    .project p{
        padding: 10px;
    }
/*Parent*/
.contact{
    border-width:1px 0;
    list-style:none;
    text-align:center;
    background-color: #888;
    border:var(--border);
    margin-top: 6px;
    -webkit-box-shadow: var(--shadows);
            box-shadow: var(--shadows);

}
/*children*/
.contact h5{
    background-color: #33e4ff;
    border-radius: 10px;
    padding: 10px 10px;
    margin: 10px;
    color: white;
    font-weight:600;
    text-align: center;
}
.contact div {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    /* align-items: center;
    flex-direction: column; */
    --icon-width:15%;
}
@media (min-width: 375px){
    .contact div {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction:row;
            flex-direction:row;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        -ms-flex-wrap: nowrap;
            flex-wrap: nowrap;
        --icon-width:20%;
    }
    .icon {
        width: var(--icon-width);
        border-left:1px solid black;
    }
    .icon:first-child{
        border-left: 0;
    }
}
.icon {
    width: var(--icon-width);
    padding:5px;
}

.icon img{
    width:50%; /* css variable */
}

.icon:hover{
    background-color: #666;
    border-radius:20%;
}



